
























@import "./../../../assets/styles/minxs.scss";
.title {
  line-height: 0.8rem;
  background: #eee;
  text-indent: 0.2rem;
}
.item-img-wrapper {
  overflow: hidden;
  height: 0;
  padding-bottom: 37.09%;
  .item-img {
    width: 100%;
  }
}
.item-info {
  padding: 0.1rem;
  .item-title {
    line-height: 0.54rem;
    font-size: 0.32rem;
    @include ellipsis;
  }
  .item-desc {
    line-height: 0.4rem;
    color: #ccc;
    @include ellipsis;
  }
}
