

























@import "../../../assets/styles/Variables.scss";
.header {
  height: 0.86rem;
  display: flex;
  line-height: $headerHeight;
  background-color: $bgColor;
  .header-left {
    width: 0.64rem;
    float: left;
    .back-icon {
      text-align: center;
      font-size: 0.4rem;
    }
  }
  .header-input {
    position: relative;
    flex: 1;
    line-height: 0.64rem;
    margin-left: 0.2rem;
    color: #ccc;
    input {
      margin-top: 0.12rem;
      box-sizing: border-box;
      padding: 0 0.2rem 0 0.62rem;
      height: 0.64rem;
      width: 100%;
      background: #fff;
      border-radius: 0.1rem;
      font-size: 0.35rem;
      &::placeholder {
        text-align: center;
      }
    }
    .search-icon {
      position: absolute;
      top: 0.12rem;
      left: 0.12rem;
      font-size: 0.5rem;
    }
  }
  .header-right {
    min-width: 1.04rem;
    padding: 0 0.1rem;
    float: right;
    text-align: center;
    color: #fff;
    .arrow-icon {
      margin-left: -0.04rem;
      font-size: 0.24rem;
    }
  }
}
